<script>

import FveText from "@fve/Element/Text/FveText";

export default {
  name: 'FveUrl',
  mixins: [
    FveText
  ],
  methods: {
    validateFunction(str) {
      // eslint-disable-next-line
      const urlRegex = /^(https?:\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/;

      if( !urlRegex.test(str) ){ return '   '; }
      return 'SUCCESS';
    },
  }
};
</script>
